import React from 'react';
import Post from './Post';

function Dashboard(props) {

	// handle click event of logout button
	const handleLogout = () => {
		props.history.push('/');
	}

	const columns = 4;

	const rows = posts => {
		return posts % columns === 0 ? posts / columns : Math.floor(posts / columns) + 1;
	}

	function RowOfColumns(props) {
		return (
			<div className="row">
				<div className="col">
					<Post name="Sara" date="2021-08-07 16:54"/>
				</div>
				<div className="col">
					<Post name="Maj" date="2021-08-07 16:54"/>
				</div>
				<div className="col">
					<Post name="Felix" date="2021-08-07 16:54"/>
				</div>
				<div className="col">
					<Post name={rows(13)} date="2021-08-07 16:54"/>
				</div>
			</div>
		);
	};

	function Grid(props) {
		return (
			<div className="container-fluid">
				{Array.from(Array(rows(props.totalPosts)).keys()).map(r => <RowOfColumns key={'row' + r}/>)}
				
			</div>
		);
	};

	return (
		<div>
			{/* <input type="button" className="button" onClick={handleLogout} value="Logout" /> */}
			

			{/* Grid system */}
			<Grid totalPosts={10}/>

		</div>
	);
}

export default Dashboard;