import React from 'react';
import { NavLink } from 'react-router-dom';
 
function Home() {
  return (
    <div>
        <h1>
            <span style={{color: "#edf2fb"}}>W</span>
            <span style={{color: "#edf2fb"}}>E</span>
            <span style={{color: "#e2eafc"}}>L</span>
            <span style={{color: "#e2eafc"}}>C</span>
            <span style={{color: "#d7e3fc"}}>O</span>
            <span style={{color: "#d7e3fc"}}>M</span>
            <span style={{color: "#ccdbfd"}}>E</span>
            <span style={{color: "#ccdbfd"}}>!</span>
        </h1>
        <p>
            Access the virtual white board here
        </p>
        <p>
            <NavLink activeClassName="active" to="/login">
                <input type="button" className="button" value={'Login'} />
            </NavLink>
        </p>
        
    </div>
  );
}
 
export default Home;